/*
 *  All styles for Blogroll shortcode
 */

.uw-body-copy ul.shortcode-blogroll {
  overflow: visible;
}

.shortcode-blogroll {
  margin-top: 40px;
  padding: 0;
  list-style: none;
  & > li {
    &:first-child {
      margin-top: 0;
    }
    margin: 20px 0 0;
    position: relative;
    overflow: hidden;
    padding-bottom: 20px;
    .wp-post-image {
      margin: 10px 30px 0 0;
      height: auto;
      width: 90px;
    }
    span {
      display: block;
      overflow: hidden;
    }
    p.date {
      font-weight: @bold;
      margin: 10px 0 0;
      font-family: @font-family-headline;
    }
    .author-info {
      margin: 0 0 20px 0;
    }
    &:after {
      display: block;
      content: "";
      width: 80px;
      height: 1px;
      background-color: #bbb;
      bottom: 0;
      position: absolute;
    }
    &:last-child:after {
      display: none;
    }
  }
  h2 {
    margin: 20px 0 0;
  }

  // Hides annoying break
  br {
    display: none;
  }

