// CSS File from Jetpack
// todo: rewrite to less
* {
  line-height: inherit; /* prevent declarations of line-height in the universal selector */
}

.jp-carousel-overlay {
  background: #000;
}

div.jp-carousel-fadeaway {
  background: -moz-linear-gradient(bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0)));
  position: fixed;
  bottom: 0;
  z-index: 2147483647;
  width: 100%;
  height: 15px;
}

.jp-carousel-next-button span,
.jp-carousel-previous-button span {
  background: url(assets/images/carousel-arrows.png) no-repeat center center;
  background-size: 200px 126px;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (-o-min-device-pixel-ratio: (3/2)), only screen and (min--moz-device-pixel-ratio: 1.5), only screen and (min-device-pixel-ratio: 1.5) {
  .jp-carousel-next-button span,
  .jp-carousel-previous-button span {
    background-image: url(./images/arrows-2x.png);
  }
}

.jp-carousel-wrap {
  font-family: "Helvetica Neue", sans-serif !important;
}

.jp-carousel-info {
  position: absolute;
  bottom: 0;
  text-align: left !important;
  -webkit-font-smoothing: subpixel-antialiased !important;
}

.jp-carousel-info ::selection {
  background: #68c9e8; /* Safari */
  color: #fff;
}

.jp-carousel-info ::-moz-selection {
  background: #68c9e8; /* Firefox */
  color: #fff;
}

.jp-carousel-photo-info {
  position: relative;
  left: 25%;
  width: 50%;
}

.jp-carousel-transitions .jp-carousel-photo-info {
  -webkit-transition: 400ms ease-out;
  -moz-transition: 400ms ease-out;
  -o-transition: 400ms ease-out;
  transition: 400ms ease-out;
}

.jp-carousel-info h2 {
  background: none !important;
  border: 0 !important;
  color: #999;
  display: block !important;
  font: normal 13px/1.25em "Helvetica Neue", sans-serif !important;
  letter-spacing: 0 !important;
  margin: 7px 0 0 0 !important;
  padding: 10px 0 0 !important;
  overflow: hidden;
  text-align: left;
  text-shadow: none !important;
  text-transform: none !important;
  -webkit-font-smoothing: subpixel-antialiased;
}

.jp-carousel-next-button,
.jp-carousel-previous-button {
  text-indent: -9999px;
  overflow: hidden;
  cursor: pointer;
}

.jp-carousel-next-button span,
.jp-carousel-previous-button span {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 82px;
  zoom: 1;
  filter: alpha(opacity=20);
  opacity: 0.2;
}

.jp-carousel-transitions .jp-carousel-next-button span,
.jp-carousel-transitions .jp-carousel-previous-button span {
  -webkit-transition: 500ms opacity ease-out;
  -moz-transition: 500ms opacity ease-out;
  -o-transition: 500ms opacity ease-out;
  transition: 500ms opacity ease-out;
}

.jp-carousel-next-button:hover span,
.jp-carousel-previous-button:hover span {
  filter: alpha(opacity=60);
  opacity: 0.6;
}

.jp-carousel-next-button span {
  background-position: -110px center;
  right: 0;
}

.jp-carousel-previous-button span {
  background-position: -10px center;
  left: 0;
}

.jp-carousel-buttons {
  margin: -18px -20px 15px;
  padding: 8px 10px;
  border-bottom: 1px solid #222;
  background: #222;
  text-align: center;
}

div.jp-carousel-buttons a {
  border: 0 !important;
  color: #999;
  font: normal 11px/1.2em "Helvetica Neue", sans-serif !important;
  letter-spacing: 0 !important;
  padding: 5px 2px 5px 0;
  text-decoration: none !important;
  text-shadow: none !important;
  vertical-align: middle;
  -webkit-font-smoothing: subpixel-antialiased;
}

div.jp-carousel-buttons a:hover {
  color: #68c9e8;
  border: 0 !important;
}

.jp-carousel-transitions div.jp-carousel-buttons a:hover {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
}

.jp-carousel-slide,
.jp-carousel-slide img,
.jp-carousel-next-button,
.jp-carousel-previous-button {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
}

.jp-carousel-slide {
  position: fixed;
  width: 0;
  bottom: 0;
  background-color: #000;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
}

.jp-carousel-transitions .jp-carousel-slide {
  -webkit-transition: 300ms ease-out;
  -moz-transition: 300ms ease-out;
  -o-transition: 300ms ease-out;
  transition: 300ms ease-out;
}

.jp-carousel-slide.selected {
  position: absolute !important;
  filter: alpha(opacity=100);
  opacity: 1;
}

.jp-carousel-slide {
  filter: alpha(opacity=25);
  opacity: 0.25;
}

.jp-carousel-slide img {
  display: block;
  width: 100% !important;
  height: 100% !important;
  max-width: 100% !important;
  max-height: 100% !important;
  background: none !important;
  border: 0 !important;
  padding: 0 !important;
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  zoom: 1;
}

.jp-carousel-transitions .jp-carousel-slide {
  -webkit-transition: opacity 400ms linear;
  -moz-transition: opacity 400ms linear;
  -o-transition: opacity 400ms linear;
  transition: opacity 400ms linear;
}

.jp-carousel-close-hint {
  color: #999;
  cursor: default;
  letter-spacing: 0 !important;
  padding: 0.35em 0 0;
  position: absolute;
  text-align: left;
  right: 20px;
}

.jp-carousel-transitions .jp-carousel-close-hint {
  -webkit-transition: color 200ms linear;
  -moz-transition: color 200ms linear;
  -o-transition: color 200ms linear;
  transition: color 200ms linear;
}

.jp-carousel-close-hint span {
  cursor: pointer;
  //background-color: black;
  background-color: rgba(0, 0, 0, 0.8);
  display: block;
  height: 22px;
  font: 400 24px/1 "Helvetica Neue", sans-serif !important;
  line-height: 22px;
  margin: 0 0 0 0.4em;
  text-align: center;
  vertical-align: middle;
  width: 22px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
}

.jp-carousel-transitions .jp-carousel-close-hint span {
  -webkit-transition: border-color 200ms linear;
  -moz-transition: border-color 200ms linear;
  -o-transition: border-color 200ms linear;
  transition: border-color 200ms linear;
}

.jp-carousel-close-hint:hover {
  cursor: default;
  color: #fff;
}

.jp-carousel-close-hint:hover span {
  border-color: #fff;
}

div.jp-carousel-buttons a.jp-carousel-like,
div.jp-carousel-buttons a.jp-carousel-reblog,
div.jp-carousel-buttons a.jp-carousel-commentlink,
a.jp-carousel-image-download {
  background: url(assets/images/carousel-sprite.png?5) no-repeat;
  background-size: 16px 200px;
}

div.jp-carousel-buttons a.jp-carousel-reblog,
div.jp-carousel-buttons a.jp-carousel-commentlink {
  margin: 0 14px 0 0 !important;
}

div.jp-carousel-buttons a.jp-carousel-reblog.reblogged,
div.jp-carousel-buttons a.jp-carousel-like.liked {
  background-color: #303030;
  padding-right: 8px !important;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
}

div.jp-carousel-buttons a.jp-carousel-reblog.reblogged {
  margin: 0 2px 0 -12px !important;
}


div.jp-carousel-buttons a.jp-carousel-reblog,
div.jp-carousel-buttons a.jp-carousel-reblog.reblogged:hover {
  background-position: 6px -36px;
  padding-left: 26px !important;
  color: #999;
}

div.jp-carousel-buttons a.jp-carousel-commentlink {
  background-position: 0 -156px;
  padding-left: 19px !important;
}

div.jp-carousel-buttons a.jp-carousel-reblog.reblogged:hover {
  cursor: default;
}

div.jp-carousel-buttons a.jp-carousel-reblog:hover {
  background-position: 6px -56px;
  color: #68c9e8;
}

div.jp-carousel-buttons a.jp-carousel-like {
  background-position: 5px 5px;
  padding-left: 24px !important;
}

div.jp-carousel-buttons a.jp-carousel-like:hover {
  background-position: 5px -15px;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (-o-min-device-pixel-ratio: 3/2), only screen and (min--moz-device-pixel-ratio: 1.5), only screen and (min-device-pixel-ratio: 1.5) {
  div.jp-carousel-buttons a.jp-carousel-like,
  div.jp-carousel-buttons a.jp-carousel-reblog,
  div.jp-carousel-buttons a.jp-carousel-commentlink,
  a.jp-carousel-image-download {
    background-image: url(./images/carousel-sprite-2x.png?5);
  }
}

/* reblog */
div#carousel-reblog-box {
