  background: -webkit-gradient(linear, left bottom, left top, from(#ccc), to(#eee));
}

#carousel-reblog-box .canceltext {
  color: #aaa;
  font-size: 11px;
  line-height: 24px;
}

#carousel-reblog-box .canceltext a {
  color: #fff;
}

