@font-face {
  font-family: "Encode Sans Normal";
  src: ~"url('@{local-font-path}@{local-font-name-normal-black}.eot')";
  src: ~"url('@{local-font-path}@{local-font-name-normal-black}.eot?#iefix') format('embedded-opentype')",
       ~"url('@{local-font-path}@{local-font-name-normal-black}.woff') format('woff')",
       ~"url('@{local-font-path}@{local-font-name-normal-black}.ttf') format('truetype')",
       ~"url('@{local-font-path}@{local-font-name-normal-black}.svg#@{local-font-name-normal-black}') format('svg')";
  font-weight: 900;
  font-style: normal;
}

