.gallery {
  clear: both;
}

.gallery-item {
  margin-bottom: 20px;
  img {
    width: 90%;
    height: auto;
    border: 0 !important;
  }
}

.uw-overlay {
  .close {
    opacity: 1;
    position: absolute;
    top: -60px;
    right: 0;
    text-indent: -9999px;
    width: 50px;
    height: 50px;
    background: url(assets/svg/uw-sprite.svg) no-repeat -424px -54px rgba(60, 60, 60, 0.13);
  }
}
